import helper from '../../common/helper.js';
export default {
	data() {
		return {
			currentTab: 0,
			essayList:null,
			shopList:null,
			swiperHeight: 0,
			bottomText: "加载更多...",
			currentPage:1,
			searchText:null
		}
	},
	methods: {
		/**
		 * @author RustyLake
		 * @version 2019-07-31
		 * @method	切换选项卡
		 * */
		switchTab: function(obj) {
			let that =this;
			console.log(obj.currentTarget.dataset.index);
			that.currentTab = obj.currentTarget.dataset.index;
			if(that.currentTab === 0){
				uni.request({
					url:helper.webroot + "/api/search/seachshops",
					method:"GET",
					data:{
						keyword:that.searchText,
						type:1,
						page:1
					},
					success:function(res){
						console.log(res);
						that.essayList = res.data.data;
					}
				})
			}else{
				uni.request({
					url:helper.webroot + "/api/search/seachshops",
					method:"GET",
					data:{
						keyword:that.searchText,
						type:2,
						page:1
					},
					success:function(res){
						console.log(res);
						that.shopList = res.data.data;
					}
				})
			}
		},
		loadMore: function(res) {
			let that = this;
			console.log('触碰到底部,加载事件');
			console.log(res);
			that.bottomText = "正在加载..."
			uni.request({
				url:helper.webroot + "/api/search/seachshops",
				method:"GET",
				data:{
					keyword:that.searchText,
					type:that.currentPage + 1,
					page:that.currentPage
				},
				success:function(res){
					console.log(res);
					if(res.data.data.length <= 0){
						that.bottomText = "已无更多文章"
					}
					that.currentPage = that.currentPage + 1;
				}
			})
			
		}
	},
	onLoad: function(option) {
		uni.showLoading({
			title:'加载中...'
		})
		this.searchText = option.keywords;
		let that = this;
		//获取文章;
		uni.request({
			url:helper.webroot + "/api/search/seachshops",
			method:"GET",
			data:{
				keyword:that.searchText,
				type:1,
				page:that.currentPage
			},
			success:function(res){
				console.log(res);
				that.essayList = res.data.data;
				that.swiperHeight = that.essayList.length * 500;
				that.currentPage = that.currentPage+1;
				uni.hideLoading();
			}
		})
	}
	
}
